ion-content.login {
  --background: #f5f5f3;
}
.login .container {
  margin-top: 60%;
}
.login .container img.logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 250px;
  width: 250px;
  border-radius: 50%;
  border: 5px solid white;
}
.login h2.titleApp {
  font-weight: 600;
  text-align: center;
  font-size: 2rem;
  text-shadow: 1px 0px 1px #cccccc, 0px 1px 1px #eeeeee, 2px 1px 1px #cccccc,
    1px 2px 1px #eeeeee, 3px 2px 1px #cccccc, 2px 3px 1px #eeeeee,
    4px 3px 1px #cccccc, 3px 4px 1px #eeeeee, 5px 4px 1px #cccccc,
    4px 5px 1px #eeeeee, 6px 5px 1px #cccccc, 5px 6px 1px #eeeeee,
    7px 6px 1px #cccccc, 2px 2px 2px rgba(206, 206, 206, 0);
  color: #444444;
  margin-top: 0;
}
.login .container .loader {
  margin-top: -50px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.login ion-spinner.loader {
  transform: scale(1.5);
}
</style>
